
import { defineComponent, PropType } from 'vue';
import { Notification_Detail } from '@/shared/type/PUM20300111.type';
import { modalController } from '@ionic/vue';
import { PUM20300112_REQ_BODY, PUM20300112_RES } from '@/shared/type/PUM20300112.type';
import { PUM20300131_REQ_BODY, PUM20300131_RES } from '@/shared/type/PUM20300131.type';

enum NotificationServiceTypeCode {
    notice = '02',
    approval = '03'
}

export default defineComponent({
    name: 'NotificationDetailPopup',
    props: {
        data: {
			type: Object as PropType<Notification_Detail>,
			required: true,
		},
    },
    data(){
        return {
            NotificationServiceTypeCode,
            detail: {
                customerSendTime: '',
                firstRegisterBranchCode: '',
                firstRegisterDate: '',
                firstRegisterTellerID: '',
                firstRegisterTime: '',
                frameworkChangeTimeStamp: '',
                lastChangeBranchCode: '',
                lastChangeDate: '',
                lastChangeTellerID: '',
                lastChangeTime: '',
                messageContent: '',
                messageContentHtml: '',
                messagePayload: '',
                messageTitle: '',
                messageTypeCode: '',
                notificationSendTime: '',
                processDate: '',
                pushIcon: '',
                transactionOccurDate: '',
                transactionOccurTime: '',
                transactionSeqNo: 0
            }
        }
    },
    mounted(){
        this.getNotificationDetail()
        this.updateNotificationReadDatetime()
    },
    methods: {
        
        //PUM20300112_Adapter_NotificationInfo_InquiryDetail
        async getNotificationDetail () {
            const reqBody : PUM20300112_REQ_BODY = {
                processDate: this.data.processDate,
                transactionSeqNo: this.data.transactionSeqNo,
            }
            const res = await this.$request.getRequest('PUM20300112', reqBody, false) as PUM20300112_RES

            if(res.header.result){
                this.detail = res.body
            }
        },

        //PUM20300131_Adapter_NotificationInfoReadDateTime_Update
        async updateNotificationReadDatetime() {
            const reqBody : PUM20300131_REQ_BODY = {
                processDate: this.data.processDate,
                transactionSeqNo: this.data.transactionSeqNo,
            }
            await this.$request.getRequest('PUM20300131', reqBody, false) as PUM20300131_RES
        },

        async onClose() {
            await modalController.dismiss(null, 'confirm')
        }
    }
})
