
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { Notification_Detail, PUM20300111_REQ_BODY, PUM20300111_RES } from "@/shared/type/PUM20300111.type";
import { defineComponent } from "vue";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { IonContent, IonInfiniteScroll, IonInfiniteScrollContent, InfiniteScrollCustomEvent, } from '@ionic/vue';
import PopupService from '@/shared/services/PopupService';
import NotificationDetailPopup from '@/shared/components/NotificationDetailPopup/NotificationDetailPopup.vue'

const popupService = new PopupService()

export default defineComponent({
    name: "pum202002sc00",
    components: {
        Pagination,
        BreadCrumb,
        IonContent,
        IonInfiniteScroll,
        IonInfiniteScrollContent,
    },
    data() {
        return {
            list: [] as Notification_Detail[],
            inputSearchTitle: '',
            filterTab: '',
            pageNumber: 1,
            totalItem: 0,
            pageSize: PAGE_SIZE,
            isReset: false,
            startingIndex: 1,
            headers: [
                { columnName: "No" },
                { columnName: "Type" },
                { columnName: "Title" },
                { columnName: "Date" },
            ]
        }
    },
    async mounted() {
        await this.getNotificationList()
    },
    methods: {
        async getNotificationList() {
            const reqBody: PUM20300111_REQ_BODY = {
                pageSize: this.pageSize,
                pageNumber: this.pageNumber,
                searchValue: this.inputSearchTitle.trim(),
                searchTypeCode: this.filterTab
            }

            const res = await this.$request.getRequest('PUM20300111', reqBody, false) as PUM20300111_RES

            if (res.header.result) {
                this.list = res.body.list
                this.totalItem = res.body.totalCount
            }
        },

        openNotificationDetail(notification: Notification_Detail) {
            popupService.onOpen({
                dataProp: {
                    data: notification
                },
                component: NotificationDetailPopup,
                callback: () => {
                    this.getNotificationList()
                }
            })
        },

        onReset(){
            this.isReset = true;
            this.pageNumber = 1
            this.inputSearchTitle = ''
            this.getNotificationList();
        },

        handlePagination(pageNumber: number) {
            this.isReset = false
            this.pageNumber = pageNumber
            this.getNotificationList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        ionInfinite(ev: InfiniteScrollCustomEvent) {
            Number(this.pageSize) < Number(this.totalItem) ? this.pageSize += PAGE_SIZE : '';
            Number(this.pageSize) <= Number(this.totalItem) ? this.getNotificationList() : '';
            setTimeout(() => ev.target.complete(), 1000);
        },
    }
});
